    ellipsis: true,
    align: 'center',
  },
  {
    title: '产品代号',
    dataIndex: 'column2',
    ellipsis: true,
    align: 'center',
  },
  {
    title: '批次',
    dataIndex: 'column3',
    ellipsis: true,
    align: 'center',
  },
